.highlight-massive {
  display: inline-block;
  position: relative;
}

.highlight-massive > .after {
  content: '';
  position: absolute;
  top: calc(100% - 4px);
  left: 0;
  width: 100%;
  height: 5px;
  background: var(--brand-primary);
  border-radius: 5px;
}

.highlight-massive-gray > .after {
  background: #E8E8E8;
  top: calc(100% - 1px);
  height: 3px;
}

.highlight-trend {
  background: var(--brand-primary);
  border-radius: 50px;
  padding: 3px 20px;
  color: #fff;
  display: inline-block;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
}

.highlight-trend + * {
  margin-top: 20px;
}

.highlight-underline {
  position: relative;
  margin-top: 30px;
  text-transform: uppercase;
  padding: 6px 0 8px 40px;
}

.highlight-underline > .before {
  content: '';
  position: absolute;
  top: -1px;
  bottom: -1px;
  left: 0;
  width: 4px;
  border-radius: 10px;
  background: var(--brand-primary);
}

.highlight-underline + .highlight-underline {
  padding-top: 0;
  margin-top: -6px;
}

.divider {
  width: 35px;
  height: 2px;
  background: var(--brand-primary);
  margin: 25px auto 32px;
  border: none;
}

.divider-light {
  width: 40px;
  height: 2px;
  background: #D4E1F6;
  margin: 20px auto 20px;
  border: none;
}

.divider-layers {
  width: 140px;
  height: 2px;
  background: #D4E1F6;
  margin: 30px auto 30px;
  border: none;
  position: relative;
}

.divider-layers > .before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  -webkit-transform: translate(-50%);
  transform: translate(-50%);
  background: var(--brand-primary);
  width: 40px;
}

.divider-cross {
  width: 12px;
  height: 12px;
  margin: 18px auto 26px;
  position: relative;
}

.divider-cross:before {
  content: '';
  position: absolute;
  top: 6px;
  right: calc(100% + 15px);
  background: #E6E6E6;
  width: 65px;
  height: 1px;
}

.divider-cross:after {
  content: '';
  position: absolute;
  top: 6px;
  left: calc(100% + 15px);
  background: #E6E6E6;
  width: 65px;
  height: 1px;
}

.divider-cross span {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -7px;
  width: 14px;
  height: 2px;
  background: var(--brand-primary);
}

.divider-cross span:first-child {
  transform: rotate(45deg);
}

.divider-cross span:last-child {
  transform: rotate(-45deg);
}

.divider-lines {
  margin-top: 45px;
}

.divider-lines span {
  display: block;
  width: 60px;
  margin: 0 auto;
  height: 1px;
  background: #272727;
}

